import ReactDOM from 'react-dom';
import css from './Popup.module.scss';

const Popup = ({ onClose }) => {
  return ReactDOM.createPortal(
    <div className={`${css.Popup} ${css.isOpen}`}>
      <button className={css.PopupClose} onClick={onClose}>
        
      </button>
      <h2></h2>
      <p></p>
    </div>,
    document.body
  );
};

export default Popup;
